<template>
  <div>
    <div class="name">运城全域智慧旅游综合管理平台</div>
    <div class="login-wrapper">
      <div class="title">用户登录</div>
      <a-form-model :model="form" @submit="handleSubmit" @submit.native.prevent>
        <a-form-model-item>
          <a-input v-model="form.user" placeholder="账号">
            <a-icon
              slot="prefix"
              type="user"
              style="color:rgba(13, 240, 254, 1)"
            />
          </a-input>
        </a-form-model-item>
        <a-form-model-item>
          <a-input v-model="form.password" type="password" placeholder="密码">
            <a-icon
              slot="prefix"
              type="lock"
              style="color:rgba(13, 240, 254, 1)"
            />
          </a-input>
        </a-form-model-item>
        <a-form-model-item>
          <a-button
            type="primary"
            html-type="submit"
            :disabled="form.user === '' || form.password === ''"
            >登录</a-button
          >
        </a-form-model-item>
      </a-form-model>
    </div>
    <div class="bg-circil">
      <img src="@/assets/circle-bg.png" alt="" class="circle" />
      <!-- <img src="@/assets/circle.png" alt="" class="circle" /> -->
    </div>
  </div>
</template>
<script>
import { message } from "ant-design-vue";

export default {
  data() {
    return {
      form: {
        user: "",
        password: "",
      },
    };
  },
  mounted() {
    this.$store.commit("setUser");
  },
  methods: {
    handleSubmit() {
      let name = this.form.user;

      message.success(`登录成功, ${name}`);
      this.$store.commit("setUser", {
        name,
      });
    },
  },
};
</script>

